<template>
  <div class="user-wrap">
    <van-nav-bar
      title="设置"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    />
    <van-form @submit="onSubmit">
      <van-field
        v-model="username"
        name="用户名"
        label="用户名"
        placeholder="用户名"
        :rules="[{ required: true, message: '请填写用户名' }]"
      />
      <van-field
        v-model="password"
        type="password"
        name="支付宝账号"
        label="支付宝账号"
        placeholder="支付宝账号"
        :rules="[{ required: true, message: '请填写支付宝账号' }]"
      />
      <van-field
        v-model="password"
        type="password"
        name="微信号"
        label="微信号"
        placeholder="微信号"
        :rules="[{ required: true, message: '请填写微信号' }]"
      />
      <div style="margin: 16px;">
        <van-button round block type="info" native-type="submit">
          提交修改
        </van-button>
      </div>
    </van-form>
  </div>
</template>

<script>
export default {
  name: 'index',
  components: {
  },
  data () {
    return {
      username: '',
      password: '',
    }
  },
  computed: {
  },
  mounted () {
  },
  methods: {
    onClickLeft () {
      this.$router.push({ path: '/user' })
    },
    onSubmit(values) {
      console.log('submit', values);
    }
  }
}
</script>



